<template>
    <div class="container">
        <alert-txt v-if="alerttxt"></alert-txt>
        <button class="closealert" @click="closealert" v-if="closebtn">关闭</button>
        <div class="logo"></div>
        <div class="nav">
            <ul>
                <li v-for="(item,index) in navlists" :key="index"><b><a :href="item.href">{{item.title}}</a></B>
                <div  class="libtn"></div>
                </li>
            </ul>
        </div>
        <div @click="searchshow" class="search"></div>
        
    </div>
</template>
<script>
import AlertTxt from '../components/alert'
export default {
    components:{
        AlertTxt
    },
    data(){
        return{
            alerttxt:false,
            closebtn:false,
            navlists:[
                {title:'ARTICLES',href:'#'},
                {title:'BRANDS',href:'#'},
                {title:'RESOURCES',href:'#'},
                {title:'CAREERS',href:'#'},
            ]
        }
    },
    methods:{
        searchshow:function(){
            this.alerttxt=true,
            this.closebtn=true
        },
        closealert:function(){
            this.alerttxt=false
            this.closebtn=false
        }
    }
    
}
</script>

<style scoped>
.closealert{
    z-index: 200;
    position: absolute;
    top:290px;
    right:41px;
}
    .container{
        padding-left:50px;
        padding-right:50px;
        display: flex;
        overflow: hidden;
        line-height:80px;
    }
    .logo{
        width:125px;
        float:left;
        background:url("../images/logo.svg") center center no-repeat;
        background-size:100%
    }
    .nav{
        flex:1;
        width:850px
    }
    .nav ul li{
        margin-left:35px;
        box-sizing: border-box;
        padding-bottom:1px;
        position: relative;
    }
    .nav ul li .libtn{
        position: absolute;
        transform:scaleX(0);
        width:100%;
        height:2px;
        background: currentColor;
        transition-duration:0.2s
    }
    .nav ul li:hover .libtn{
        transform:scaleX(1)
    }
    .search{
        width:60px;
        background:url("../images/search.png") no-repeat;
        float: right;
        background-size:50%;
        margin-top:40px
    }
    .alerttxts{
        display:none
    }
</style>





