<template>
  <div id="app">
    <m-header></m-header>
    <m-content></m-content>
    <router-view/>
    <m-footer></m-footer>
    
  </div>
</template>

<script>
import MHeader from "./components/header"
import MFooter from "./components/footer"
import MContent from "./components/content"
export default {
  components:{
    MHeader:MHeader,
    MFooter:MFooter,
    MContent:MContent,
  },

  name: 'App'
}
</script>

<style>

ul li{
  list-style: none;
  display:inline-block;
}
a{
  color:#000;
  text-decoration: none
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  overflow: hidden;
}
</style>



