<template>
    <div class="alerttxt">
        <div class="alertsearch">
            <input type="text" class="txt-search" placeholder="keywords" style="outline:none;">
        </div>
            
        </div>
</template>
<script>
export default {
    methods:{
    }
}
</script>

<style>

    .alerttxt{
        position:absolute;
        width:95%;
        height:100%;
        background:rgba(255, 255, 255, 0.9);
        z-index: 200;
        overflow: hidden;
    }
    .alertsearch{
        width:100%;
        border-bottom:1px solid #ccc;
        margin:25% auto;
        position:relative;
    }
    .alertsearch .txt-search{
        width:98%;
        height:65px;
        font-size: 30px;
        border:0;
        text-align:center;
        position: absolute;
        top:-68px;
        right:0px;
        animation: move linear .3s
    }
    @keyframes move{
        from{top:100px}
        to{top:-67px}
    }
    .alertsearch .txt-search:active{
        box-shadow:none;
        outline:none
    }
</style>




