<template>
    <div class="container">
        <div class="left">
            <ul>
                <li v-for="(list,index) in FLlist" :key="index"><a href="list.href">{{list.title}}</a></li>
            </ul>
        </div>
        <div class="code">
            <ul>
                <li class="weixin" @mouseover="showcode" @mouseout="hidecode">微信</li>
                <li class="weibo">微博</li>
            </ul>
            <transition name="fade">
                <div class="weixinimg"><img v-if="showcodes"  src="../images/isux-qr.png"></div>
            </transition>
        </div>
        <h4>Copyright © 1998 - 2018 Tencent Inc. All Rights Reserved.</h4>
    </div>
</template>
<script>
export default {
    data(){
        
        return{
            showcodes:false,
            FLlist:[
                {title:'Tencent',href:'#'},
                {title:'QQfamily',href:'#'},
                {title:'YCG',href:'#'}
            ]
        }
    },
    methods:{
        showcode:function(){
            this.showcodes=true
        },
        hidecode:function(){
            this.showcodes=false
        }
    }
}
</script>
<style>
.container{
    display:flex
}
.left,.code,.right{
    flex: 1
}
.weixin,.weibo{
    z-index: 200;
}
.left ul li{
    margin-left:15px
}
.code{
    flex: 1;
    position: relative;
}
.code ul li{
    cursor: pointer;
}
.fade-enter-active, .fade-leave-active {
 transition: opacity 1.5s
}
.fade-enter, .fade-leave-active {
 opacity: 0
}
.weixinimg{
    margin-top:-268px
}
.right{
    flex: 1
}
</style>





