import Vue from 'vue'
import App from './App'
import router from './router'
import VueLazyload from 'vue-lazyload'



Vue.use(VueLazyload, {
  preLoad: 1.3,
  error:require('./images/content/26073952_Vd2W.gif'),
  loading:require('./images/content/26073952_Vd2W.gif'),
  
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



