    .contentlist{
        width:100%;
        padding:0 20px;
        overflow: hidden;
    }
    .contentlist ul{
        width:100%;
        padding:0
    }
    .contentlist ul li{
        position: relative;
        overflow: hidden;
        cursor:pointer;
        float:left
    }
    .contentlist ul li h2{
        position: absolute;
        bottom:-14px;
        left:0;
        right:0;
        font-size:20px;
        z-index: 2
    }
    .contentlist ul li:hover img{
        transform: scale(1.1);
        filter: none; 
    }
    .contentlist ul li img{
        width:100%;
        top:0;
        left:0;
        transition-duration: 1s;
        filter: grayscale(100%); 
    }
    .contentlist ul li:hover h2{
        font-weight: bold;
        color:black
    }
